// var _ = require('underscore')

angular.module('well', [
  'ngRoute',
  'ngAnimate',
  'well.controllers',
  'well.filters',
  'well.services',
  'well.directives',
  'well.survey',
  'well.tradeoff',
  'well.overlap',
  'well.instruct',
  'well.results',
  'well.instruct',
  'well.modular',
  'well.admin'
])
.config(function ($routeProvider, $locationProvider) {
  $routeProvider
    .when('/', {
      templateUrl: 'landing.html'
    })
    .when('/auth', {
      templateUrl: 'auth.html'
    })
    .when('/consent', {
      templateUrl: 'consent.html'
    })
    .when('/terms', {
      templateUrl: 'terms.html'
    })
    .when('/privacy', {
      templateUrl: 'privacy.html'
    })
    // survey
    .when('/:module/survey', {
      templateUrl: 'survey.html',
      controller: 'SurveyCtrl'
    })
    .when('/sur', {
      templateUrl: 'survey.html',
      controller: 'SurveyCtrl'
    })
    .when('/transition', {
      templateUrl: 'transition.html',
      controller: 'TransCtrl'
    })
    // instructions
    .when('/:module/instruct', {
      templateUrl: 'instructions.html',
      controller: 'InstructCtrl'
    })
    .when('/:module/instructions', {
      templateUrl: 'instruct.html',
      controller: 'InstructorCtrl'
    })
    // ThreeTwo Module
    .when('/threetwo/abbcca', {
      templateUrl: 'tradeoff.html',
      controller: 'ThreeCtrl'
    })
    .when('/threetwo/dbec', {
      templateUrl: 'tradeoff.html',
      controller: 'TwoCtrl'
    })
    // Overlap Module
    .when('/overlap/transition', {
      templateUrl: 'transition.html',
      controller: 'TransCtrl'
    })
    .when('/overlap/abac', {
      templateUrl: 'tradeoff.html',
      controller: 'AbacCtrl'
    })
    .when('/overlap/abc', {
      templateUrl: 'overlap.html',
      controller: 'AbcCtrl'
    })
    // Fine Overlap Module
    .when('/fineoverlap/tradeoff', {
      templateUrl: 'fineoverlap.html',
      controller: 'FineCtrl'
    })
    // Modular
    .when('/module/config', {
      templateUrl: 'config.html',
      controller: 'ConfigCtrl'
    })
    .when('/module/:name/rating', {
      templateUrl: 'rating.html',
      controller: 'RatingCtrl'
    })
    .when('/module/:name/tradeoff', {
      templateUrl: 'modulartradeoff.html',
      controller: 'TradeoffCtrl'
    })
    // exit
    .when('/:module/exit', {
      templateUrl: 'exit.html',
      controller: 'ExitCtrl'
    })
    // Misc
    .when('/chart', {
      templateUrl: 'chart.html',
      controller: 'ChartCtrl'
    })
    .when('/dummy', {
      templateUrl: 'dummy.html',
      controller: 'DummyCtrl'
    })
    .when('/test', {
      templateUrl: 'test.html',
      controller: 'TestCtrl'
    })
    .when('/admin/dashboard', {
      templateUrl: 'admin.html',
      controller: 'AdminCtrl'
    })
    .when('/anon', {
      templateUrl: 'landing.html',
      resolve: {
        login: function (User) {
          return User.loginAnon()
        }
      }
    })
    .otherwise({
      redirectTo: '/'
    })

  // $locationProvider.html5Mode(true)
})
.run(function (User, $location, $http) {
  if (!User.isAuthed() && $location.path().indexOf('/admin') !== - 1) $location.path('/')
  $http.post('api/survey/generate', {
    aspects: [{
      text: 'Health',
      rating: 72
    },{
      text:'Meaning',
      rating: 79
    },{
      text:'Worthwhileness',
      rating: 76
    }],
    combinations: [{
     type: 'pairwise',
     indices: [0,1] 
    },{
     type: 'pairwise',
     indices: [0,2] 
    },{
     type: 'overlap',
     indices: [0,1,2] 
    }]
  })
    .success(function (response) {
      console.log(response)
    })
    .error(function (error) {
      console.log(error)
    })
})
