angular.module('well.modular', [])  
  .controller('ConfigCtrl', function ($scope, $timeout) {
    $scope.range = d3.range

    $scope.survey = {
      info: {
        title: '',
        author: ''
      },
      aspects: [ [] ],
      addAspects: (bucket, aspects) => {
        $scope.survey.aspects[bucket] = _.union($scope.survey.aspects[bucket], aspects)
        $scope.nextAspects = null
      },
      addBucket: () => {
        $scope.survey.aspects.push([])
      },
      removeBucket: (index) => {
        $scope.survey.aspects.splice(index,1)
        $scope.survey.samples.splice(index,1)
      },
      samples: [],
      tasks: [],
      newCombo: {
        type: 'pairwise',
        aspects: []
      },
      addCombo: () => {
        if ($scope.survey.newCombo.aspects.length > 1) {
          $scope.survey.tasks.push(_.merge({}, $scope.survey.newCombo))
        }
      },
      preamble: {
        type: 'preamble',
        text: 'On each of the following screens, we will ask you to choose between two different ways in which your life might change. On each screen you will have two options to choose from. In each option, levels of some of the aspects of your life that you have just rated will either increase or decrease.'
      },
      addPreamble: () => {
        $scope.survey.tasks.push(_.merge({}, $scope.survey.preamble))
      },
      language: {
        option: {
          one: 'Option 1',
          two: 'Option 2'
        },
        prefer: {
          one: 'I Prefer Option 1',
          two: 'I Prefer Option 2'
        }
      }
    }

    $scope.$watch('user.info', () => {
      $timeout(() => {
        $scope.survey.info.author = $scope.user.info.password.email
      })
    })

    $scope.$watch('survey.samples', () => {
      $timeout(() => {
        $scope.allSamples = $scope.survey.aspects.reduce((a, b, i) => {
          return a.concat(d3.range(1,$scope.survey.samples[i] + 1)
            .map((n, j) => String.fromCharCode(i + 97) + n)
          )
        }, [])
      })
    }, true)

    $scope.configuration = {
      info: $scope.survey.info,
      aspects: $scope.survey.aspects,
      samples: $scope.survey.samples,
      tasks: $scope.survey.tasks,
      language: $scope.survey.language
    }
  })
  .factory('Choice', function (User, Aspects, Survey) {
    var choice = {}
    return choice
  })