/* Filters */

angular.module('well.filters', [])
  .filter('round2', function () {
    return function (n) {
      return Number(n).toFixed(2)
    }
  })
  .filter('round1', function () {
    return function (n) {
      return n % 1 === 0 ? n : Number(n).toFixed(1)
    }
  })
  .filter('percent', function () {
    return function (n) {
      return d3.format('%')(n)
    }
  })
  .filter('capitalize', function () {
    return function (string) {
      return string ? string.charAt(0).toUpperCase() + string.slice(1) : ''
    }
  })
  .filter('duration', function () {
    return function (n) {
      var s = Math.round(n / 1000)
      return s < 10 ? '0:0' + s : '0:' + s
    }
  })
  .filter('JSONify', function () {
    return function (j) {
      return angular.toJson(j, 2)
    }
  })
  .filter('alphabetize', function () {
    return function (i) {
      return String.fromCharCode(i + 97)
    }
  })
  .filter('greekify', function () {
    return function (s) {
      var code = s.charCodeAt(0)
      return unescape('%u0' + (848 + code).toString(16)) + s.substring(1)
    }
  })
  // formatters
  .directive('comma', function () {
    return {
        require: 'ngModel',
        link: function(elem, $scope, attrs, ngModel){
            // ngModel.$formatters.push(function(text){
            //   return text.replace(/,/g, '\n')
            // })
            ngModel.$parsers.unshift(function(text){
              return text.split(/\s*,\s*/g)
            })
        }
    }
})